body {
	margin: 0px;
	padding: 0;
	background: #101010 url("./images/img06.jpg") no-repeat left bottom;
	font-family: Arial, Helvetica, sans-serif;
	font-size: 12px;
	color: #72809F;
}

h1, h2, h3 {
	margin: 0;
	font-weight: normal;
	color: #1E3F7F;
}

h1 {
	font-size: 44px;
}

h2 {
	font-size: 18px;
}

h3 {
}

p, ul, ol {
	margin-top: 0;
	line-height: 240%;
	text-align: justify;
}

ul, ol {
}

blockquote {
}

a {
	color: #ADBFE1;
}

a:hover {
	text-decoration: none;
}

a img {
	border: none;
}

img.left {
	float: left;
	margin: 7px 30px 0 0;
}

img.right {
	float: right;
	margin: 7px 0 0 30px;
}

hr {
	display: none;
}

.clearfix {
	clear: both;
}

.list1 {
}

.list1 li {
	float: left;
	line-height: normal;
}

.list1 li img {
	margin: 0 30px 30px 0;
}

.list1 li.alt img {
	margin-right: 0;
}

#wrapper {
}

/* Header */

#header {
	width: 960px;
	height: 160px;
	margin: 0px auto;
	background: url("./images/img01.jpg") no-repeat left top;
}

/* Menu */

#menu {
	float: left;
	width: 900px;
	height: 44px;
}

#menu ul {
	margin: 0;
	padding: 0px 0px 0px 0px;
	list-style: none;
	line-height: normal;
}

#menu li {
	display: block;
	float: left;
	background: url("./images/img05.jpg") no-repeat right top;
}

#menu a {
	display: block;
	float: left;
	height: 34px;
	margin-right: 1px;
	padding: 30px 30px 0px 30px;
	letter-spacing: -1px;
	text-decoration: none;
	text-align: center;
	text-transform: lowercase;
	font-family: Arial, Helvetica, sans-serif;
	font-size: 12px;
	font-weight: normal;
	color: #FFFFFF;
}

#menu a:hover {
	text-decoration: none;
}

#menu .current_page_item a {
}

#menu li.first {
	background: none;
}

/* Search */

#search {
	float: right;
}

#search form {
	float: right;
	margin: 0;
	padding: 4px 0px 0 0;
}

#search fieldset {
	margin: 0;
	padding: 0;
	border: none;
}

#search input {
	float: left;
	font: 12px Georgia, "Times New Roman", Times, serif;
}

#search-text {
	width: 213px;
	height: 28px;
	padding: 6px 0 0 7px;
	border: none;
	color: #000000;
}

#search-submit {
	width: 82px;
	height: 28px;
	margin-left: 10px;
	padding: 0px 5px;
	border: none;
	text-indent: -9999px;
	color: #FFFFFF;
}

/* Page */

#page {
	width: 960px;
	margin: 0px auto 20px auto;
	padding: 0px;
}

/** LOGO */

#logo {
	width: 960px;
	height: 100px;
	margin: 0 auto;
	padding: 0px 0px 20px 0px;
	text-transform: lowercase;
}

#logo h1, #logo h2 {
	margin: 0px;
	line-height: normal;
	font-family: Georgia, "Times New Roman", Times, serif;
	color: #FFFFFF;
}

#logo h2 {
	color: #FFFFFF;
}

#logo h1 a {
	color: #FFFFFF;
}

#logo h1 {
	padding-top: 80px;
	font-size: 52px;
	font-weight: normal;
	font-style: italic;
	color: #FFFFFF;
}

#logo a {
	text-decoration: none;
	color: #FFFFFF;
}

#banner {
	padding-bottom: 20px;
}

/* Content */

#content {
	float: right;
	width: 640px;
	margin: 0px 0px 0px 0px;
	padding: 40px 30px 0px 20px;
}

/* Post */

.post {
	margin-bottom: 45px;
	padding-bottom: 30px;
	border-bottom: #203253 dashed 1px;
}

.post .title {
	border-bottom: 4px dashed #203253;
}

.post .title a {
	display: block;
	padding: 0px 0px 10px 0px;
	text-decoration: none;
	text-transform: capitalize;
	text-transform: uppercase;
	font-weight: bold;
	font-size: 28px;
	color: #FFFFFF;
}

.post .date {
	display: inline-block;
	margin: 0px;
	padding: 0px 0px;
	color: #FFFFFF;
}

.post .meta {
	margin-left: 2px;
	padding: 4px 30px 2px 0px;
	font-family: Arial, Helvetica, sans-serif;
	font-weight: normal;
	font-size: 10px;
	font-style: italic;
}

.post .meta span {
	margin: 0px;
}

.post .meta a {
	padding: 6px 10px 5px 10px;
	text-decoration: none;
	color: #FFFFFF;
}

.post .entry {
	padding: 10px 0px 20px 0px;
}

.post .links {
	height: 18px;
	margin: 0px 0px 4px 0px;
	padding: 6px 10px 5px 10px;
	background: #182739;
	text-decoration: none;
	font-family: Arial, Helvetica, sans-serif;
	font-size: 11px;
	text-decoration: none;
	font-weight: bold;
	color: #FFFFFF;
}

.post .comments {
	display: block;
	width: 120px;
	height: 18px;
	margin: 0px;
	padding: 3px 0px 0px 40px;
	border: 1px solid #5F817E;
	text-decoration: none;
	font-family: Arial, Helvetica, sans-serif;
	font-size: 11px;
	text-decoration: none;
	font-weight: bold;
	color: #FFFFFF;
}

/* Sidebar */

#sidebar {
	float: left;
	width: 250px;
	padding: 40px 0px 0px 0px;
	font-family: Arial, Helvetica, sans-serif;
	font-size: 12px;
}

#sidebar ul {
	margin: 0;
	padding: 0;
	list-style: none;
	line-height: normal;
}

#sidebar li {
	margin: 0px;
	padding: 0 0 0px 0px;
}

#sidebar li ul {
	margin: 0px 0px 30px 0px;
}

#sidebar li li {
	margin: 0px 20px;
	padding: 10px 0px 10px 0px;
	background: none;
	border-bottom: #203253 dashed 1px;
}

#sidebar li li a {
	font-weight: normal;
}

#sidebar li li a:hover {
}

#sidebar p {
	margin: 0px;
	padding: 20px 20px;
}

#sidebar li h2 {
	display: block;
	height: 37px;
	margin: 0px;
	padding: 15px 0px 0px 20px;
	background: url("./images/img02.jpg") no-repeat left top;
	text-transform: uppercase;
	font-size: 18px;
	font-weight: bold;
	color: #FFFFFF;
}

#sidebar p {
	line-height: 200%;
}

#sidebar a {
	text-align: left;
	text-decoration: none;
	color: #71809E;
}

/* Calendar */

#calendar {
}

#calendar caption {
	padding-bottom: 5px;
	font-weight: bold;
}

#calendar table {
	width: 100%;
	border-collapse: collapse;
	border-bottom: 1px solid #24130F;
	border-left: 1px solid #24130F;
	border-right: 1px solid #24130F;
}

#calendar thead th {
	padding: 5px 0;
	text-align: center;
	border-top: 1px solid #24130F;
	border-left: 1px solid #24130F;
	background: #24130F;
}

#calendar tbody td {
	padding: 5px 0;
	text-align: center;
	border-top: 1px solid #24130F;
	border-left: 1px solid #24130F;
	border-bottom: 1px solid #24130F;
}

#calendar tfoot td {
	padding: 5px;
	border-left: 1px solid #24130F;
	border-bottom: 1px solid #24130F;
}

#calendar tfoot #next {
	border-top: 1px solid #24130F;
	text-align: right;
}

#calendar tfoot #prev {
	border-top: 1px solid #24130F;
}

#calendar .pad {
	border-bottom: 1px solid #24130F;
}

#calendar #today {
	background: #24130F;
}

/* Footer */

#footer {
	width: 920px;
	height: 60px;
	margin: 0 auto;
	padding: 0;
}

#footer-bgcontent {
	height: 110px;
	/* background: url("./images/img02.gif") repeat-x left top;  */
}

#footer p {
	margin: 0;
	padding: 28px 0px 0px 0px;
	text-align: center;
	line-height: normal;
	font-size: 12px;
}

#footer a {
}

#bg1 {
	background: url("./images/img03.jpg") repeat-y left top;
}

#bg2 {
	margin-bottom: 10px;
	padding: 10px 0px 10px 0px;
	background: url("./images/img04.jpg") no-repeat left bottom;
}
